/**
Font weights
light: 300
regular: 400;
SemiBold: 600;
Bold: 700
ExtraBold: 800;
Black: 900;
*/
:root {
  --inner-width:1158px;
  --inner-width-narrow:1078px;
  --section-padding-top:80px;
  --section-padding-bottom:50px;
  --gutter-padding:40px;
  --margin-small:30px;
  --margin-large:50px;
  --margin-extra-large: 100px;
