/* Typography
--------------------------------------------- */
body,
button,
input,
select,
optgroup,
textarea {
  color: black;
  font-family: "Montserrat", sans-serif;
  font-size: max(1rem, 0.929rem + 0.36vw, 1.25rem);
  line-height: 1.3;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 0.9;
  margin-top: 0;
}

h1, h2 {
  font-size: max(3rem, 2.643rem + 1.79vw, 4.25rem);
  font-weight: 800;
  margin-bottom: 1.5rem;
}
@media (min-width: 481px) {
  h1, h2 {
    margin-bottom: 2rem;
  }
}
@media (min-width: 768px) {
  h1, h2 {
    margin-bottom: 3rem;
  }
}

h3, .h3 {
  font-size: max(2.125rem, 2.089rem + 0.18vw, 2.25rem);
  font-weight: 900;
}

h4 {
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: 600;
}

h5 {
  font-size: 12px;
  font-size: 0.75rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  line-height: 1;
}

p {
  margin-top: 0;
  margin-bottom: 1.5em;
  font-weight: 300;
}

.uppercase {
  text-transform: uppercase;
}

.standfirst,
.standfirst p {
  font-size: max(1.25rem, 1.179rem + 0.36vw, 1.5rem);
  font-weight: 600;
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  margin: 0 1.5em;
}

address {
  margin: 0 0 1.5em;
  font-style: normal;
}

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", courier, monospace;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1.6em;
}

code,
kbd,
tt,
var {
  font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
}

abbr,
acronym {
  border-bottom: 1px dotted #666;
  cursor: help;
}

mark,
ins {
  background: #fff9c0;
  text-decoration: none;
}

big {
  font-size: 125%;
}

/* Elements
--------------------------------------------- */
.outer-grid-container {
