  grid-template-columns: 1fr repeat(14, minmax(0, 82.7142857143px)) 1fr;
  gap: 20px;
  padding: 0 40px;
}
@media (min-width: 768px) {
  .outer-grid {
    padding: 0;
    gap: 30px;
  }
}
.outer-grid > div {
  border: 1px solid red;
}
.outer-grid .tText {
  grid-column: 1/span 16;
}
@media (min-width: 768px) {
  .outer-grid .tText {
    grid-column: 4/span 9;
    position: relative;
  }
  .outer-grid .tText:before {
    content: url(./gfx/bg-our-brands.svg);
    position: absolute;
    left: -5rem;
    top: -100px;
  }
}
@media (min-width: 1190px) {
  .outer-grid .tText:before {
    left: -5rem;
    top: -100px;
  }
}
.outer-grid .cText {
  grid-column: 1/span 16;
}
@media (min-width: 768px) {
  .outer-grid .cText {
    grid-column: 6/span 7;
  }
}
@media (min-width: 1440px) {
  .outer-grid .cText {
    grid-column: 6/span 6;
  }
}
.outer-grid .gContent {
  grid-column: 1/span 16;
}
@media (min-width: 768px) {
  .outer-grid .gContent {
    grid-column: 6/span 10;
  }
}
@media (min-width: 1190px) {
  .outer-grid .gContent {
    grid-column: 6/span 8;
  }
}
@media (min-width: 1440px) {
  .outer-grid .gContent {
    grid-column: 6/span 7;
  }
}
.outer-grid .gCarousel {
  grid-column: 1/span 16;
}
@media (min-width: 768px) {
  .outer-grid .gCarousel {
    grid-column: 4;
    grid-column-end: -1;
  }
}

.inner-grid {
