/* Links
--------------------------------------------- */
a {
  color: #00599D;
  text-decoration: none;
}
a:visited {
  color: #00599D;
}
a:hover, a:focus, a:active {
  color: #0076d0;
}
a:focus {
  outline: thin dotted;
}
a:hover, a:active {
  outline: 0;
}
a.purple {
  color: #4A2670;
  font-weight: 700;
}
a.purple:visited {
  color: #4A2670;
}
a.purple:hover, a.purple:focus, a.purple:active {
  color: #633396;
}
a.purple:hover .arrow-purple, a.purple:focus .arrow-purple, a.purple:active .arrow-purple {
  fill: #633396;
}
a.purple:focus {
  outline: thin dotted;
}

.entry-content a {
