.slick-next {
  margin-left: 10px;
}

.slick-prev {
  transform: scaleX(-1);
}

/*--------------------------------------------------------------
# Utilities
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text,
.sr-only {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}
.screen-reader-text:focus,
.sr-only:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  clip-path: none;
  color: black;
  display: block;
  font-size: 0.875rem;
  font-weight: 700;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

/* Do not show the outline on the skip link target. */
#primary[tabindex="-1"]:focus {
  outline: 0;
}

.alignleft {
  /*rtl:ignore*/
  float: left;
  /*rtl:ignore*/
  margin-right: 1.5em;
  margin-bottom: 1.5em;
}

.alignright {
  /*rtl:ignore*/
  float: right;
  /*rtl:ignore*/
  margin-left: 1.5em;
  margin-bottom: 1.5em;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1.5em;
}

.align-right {
  margin-left: auto;
}

.bg-image {
  background-repeat: no-repeat;
}

.arrow-black {
  fill: black;
}

.arrow-green {
  fill: #3DAB36;
}

.arrow-purple {
  fill: #4A2670;
}

.arrow-down {
  transform: rotate(90deg);
}

.arrow-left {
  transform: rotate(180deg);
}

.bg {
  padding-top: 80px;
  padding-bottom: 50px;
  position: relative;
}

.bg-img {
  background-repeat: no-repeat;
  background-size: cover;
}

.bg-parallax {
  padding-top: 80px;
  overflow: hidden;
  position: relative;
}

.parallaxBG {
  color: white;
  font-weight: 900;
  font-size: 100px;
  font-size: 6.25rem;
  text-transform: uppercase;
  line-height: 0.8;
  opacity: 0.1;
  position: absolute;
  left: -30px;
  transform: rotate(90deg);
  pointer-events: none;
}
@media (max-width: calc(768px - 1px)) {
  .parallaxBG {
    display: none;
  }
}

.parallaxContent,
.copy-img {
  position: relative;
}

.bgImg-title-panel {
  height: 70vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center 50px;
}

.img-left__img {
  position: absolute;
  width: 100%;
  inset: 0;
  display: flex;
}
.img-left__img img {
  object-fit: cover;
  object-position: right;
  width: 100%;
  max-width: 55%;
}
@media (max-width: calc(768px - 1px)) {
  .img-left__img {
    display: none;
  }
}

@media (min-width: 768px) {
  .img-left__copy {
    display: grid;
    grid-template-columns: 60% 40%;
  }
}
@media (min-width: 768px) {
  .img-left__copy .img__copy {
    grid-column-start: 2;
  }
}

.img-right__img {
  position: absolute;
  width: 100%;
  inset: 0;
}
.img-right__img img {
  object-fit: cover;
  object-position: right;
  width: 100%;
  max-width: 55%;
}
@media (max-width: calc(768px - 1px)) {
  .img-right__img {
    display: none;
  }
}

@media (min-width: 768px) {
  .img-right__copy {
    display: grid;
    grid-template-columns: 60% 40%;
  }
}
@media (min-width: 768px) {
  .img-right__copy .img__copy {
    grid-column-start: 1;
  }
}

@media (min-width: 768px) {
  .copy-image {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 90px;
    align-items: center;
  }
}

@media (max-width: calc(768px - 1px)) {
  .copy-image__image img {
    width: 100%;
  }
}

@media (min-width: 768px) {
  .overlaps-top-bottom {
    margin: -100px 0;
  }
}
@media (max-width: calc(768px - 1px)) {
  .overlaps-top-bottom {
    margin-bottom: -100px;
  }
}

@media (min-width: 768px) {
  .is-overlapped {
    padding-top: 100px;
    padding-bottom: 150px;
  }
}

.copy-and-image-grid {
  display: grid;
  grid-template-columns: 40px auto 40px;
}
@media (min-width: 768px) {
  .copy-and-image-grid {
    display: grid;
    grid-template-columns: minmax(40px, 1fr) minmax(auto, 578px) minmax(auto, 578px) minmax(40px, 1fr);
  }
  .copy-and-image-grid.__has-panel, .copy-and-image-grid.__image-left {
    grid-template-columns: minmax(40px, 1fr) minmax(auto, 678px) minmax(auto, 478px) minmax(40px, 1fr);
  }
}

.copy-and-image-grid__copy {
  position: relative;
  grid-column-start: 2;
  margin: 50px 0 20px;
}
@media (min-width: 768px) {
  .copy-and-image-grid__copy {
    margin: 50px 0;
  }
}
@media (min-width: 768px) {
  .copy-and-image-grid__copy.__panel {
    padding: 0 50px 0 100px;
  }
  .copy-and-image-grid__copy.__panel .parallaxBG {
    top: 112px;
    left: -170px;
  }
}
.copy-and-image-grid__copy.__image_left {
  margin-top: 100px;
}
@media (min-width: 768px) {
  .copy-and-image-grid__copy.__image_left {
    grid-area: 1/3;
    padding-left: 100px;
  }
  .copy-and-image-grid__copy.__image_left .parallaxBG {
    top: 100px;
    left: -150px;
  }
}

.copy-and-image-grid__img {
  display: none;
}
@media (min-width: 768px) {
  .copy-and-image-grid__img {
    display: block;
    grid-column-start: 3;
    grid-column-end: -1;
  }
}
.copy-and-image-grid__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: left;
}
.copy-and-image-grid__img.__panel {
  display: block;
  grid-column-start: 2;
  margin: 0 0 -75px 0;
}
@media (min-width: 768px) {
  .copy-and-image-grid__img.__panel {
    margin: -50px 0;
    display: block;
    grid-column-start: 3;
    grid-column-end: auto;
    z-index: 100;
  }
}
.copy-and-image-grid__img.__panel img {
  object-position: center;
}
.copy-and-image-grid__img.__image_left {
  overflow: hidden;
}
@media (min-width: 768px) {
  .copy-and-image-grid__img.__image_left {
    grid-area: 1/1/3/4;
  }
}

@media (min-width: 768px) {
  .two-col-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 75px;
  }
}

